import React, { FC, HTMLAttributes } from 'react'
import classNames from 'classnames'

export interface CBreadcrumbProps extends HTMLAttributes<HTMLOListElement> {
  /**
   * A string of all className you want applied to the component. [docs]
   */
  className?: string
}

export const CBreadcrumb: FC<CBreadcrumbProps> = ({ children, className, ...rest }) => (
  <nav aria-label="breadcrumb" className={classNames(className)} {...rest}>
    <ol className="breadcrumb">{children}</ol>
  </nav>
)
