import React, { ElementType, FC, HTMLAttributes } from 'react'
import classNames from 'classnames'

export interface CHeaderBrandProps extends HTMLAttributes<HTMLAnchorElement | HTMLSpanElement> {
  /**
   * A string of all className you want applied to the component. [docs]
   */
  className?: string
  /**
   * Component used for the root node. Either a string to use a HTML element or a component. [docs]
   *
   * @default 'a'
   */
  component?: string | ElementType
}

export const CHeaderBrand: FC<CHeaderBrandProps> = ({
  children,
  component: Component = 'a',
  className,
  ...rest
}) => {
  const _className = classNames('header-brand', className)

  return (
    <Component className={_className} {...rest}>
      {children}
    </Component>
  )
}
