import React, { FC, HTMLAttributes } from 'react'
import classNames from 'classnames'

export interface CProgressBarProps extends HTMLAttributes<HTMLDivElement> {
  /**
   * Use to animate the stripes right to left via CSS3 animations. [docs]
   *
   * @default false
   */
  animated?: boolean
  /**
   * A string of all className you want applied to the component. [docs]
   */
  className?: string
  /**
   * Sets the color context of the component to one of CoreUI’s themed colors. [docs]
   *
   * @default 'primary'
   */
  color: string
  /**
   * The percent to progress the ProgressBar. [docs]
   *
   * @default 0
   */
  value?: number
  /**
   * Set the progress bar variant to optional striped. [docs]
   *
   * @default 'undefined'
   */
  variant?: 'striped'
}

export const CProgressBar: FC<CProgressBarProps> = ({
  children,
  animated = false,
  className,
  color = 'primary',
  value = 0,
  variant,
  ...rest
}) => {
  const _className = classNames(
    'progress-bar',
    `bg-${color}`,
    {
      [`progress-bar-${variant}`]: variant,
      ['progress-bar-animated']: animated,
    },
    className,
  )

  return (
    <div
      className={_className}
      role="progressbar"
      style={{ width: `${value}%` }}
      aria-valuenow={value}
      aria-valuemin={0}
      aria-valuemax={100}
      {...rest}
    >
      {children}
    </div>
  )
}
