import React, { ElementType, FC, HTMLAttributes } from 'react'
import classNames from 'classnames'

export interface CDropdownItemPlainProps extends HTMLAttributes<HTMLSpanElement> {
  /**
   * A string of all className you want applied to the component. [docs]
   */
  className?: string
  /**
   * Component used for the root node. Either a string to use a HTML element or a component. [docs]
   *
   * @default 'span'
   */
  component?: string | ElementType
}

export const CDropdownItemPlain: FC<CDropdownItemPlainProps> = ({
  children,
  className,
  component: Component = 'span',
  ...rest
}) => {
  const _className = classNames('dropdown-item-text', className)

  return (
    <Component className={_className} {...rest}>
      {children}
    </Component>
  )
}
