import React, { FC, HTMLAttributes } from 'react'
import classNames from 'classnames'

export interface CNavbarTextProps extends HTMLAttributes<HTMLSpanElement> {
  /**
   * A string of all className you want applied to the base component. [docs]
   */
  className?: string
}

export const CNavbarText: FC<CNavbarTextProps> = ({ children, className, ...rest }) => {
  const _className = classNames('navbar-text', className)

  return (
    <span className={_className} {...rest}>
      {children}
    </span>
  )
}
