import React, { FC, HTMLAttributes } from 'react'
import classNames from 'classnames'
import { CProgressBar, CProgressBarProps } from './CProgressBar'

export interface CProgressProps
  extends Omit<HTMLAttributes<HTMLDivElement>, 'color'>,
    CProgressBarProps {
  /**
   * A string of all className you want applied to the component. [docs]
   */
  className?: string
  /**
   * Sets the height of the component. If you set that value the inner <CProgressBar> will automatically resize accordingly. [docs]
   *
   * @default 'undefined'
   */
  height?: number
}

export const CProgress: FC<CProgressProps> = ({ children, className, height, value, ...rest }) => {
  const _className = classNames('progress', className)

  return (
    <div className={_className} style={height ? { height: `${height}px` } : {}}>
      {value ? (
        <CProgressBar value={value} {...rest}>
          {children}
        </CProgressBar>
      ) : (
        children
      )}
    </div>
  )
}
