import React, { FC, ButtonHTMLAttributes } from 'react'
import classNames from 'classnames'

export interface CAccordionButtonProps extends ButtonHTMLAttributes<HTMLButtonElement> {
  /**
   * A string of all className you want applied to the base component. [docs]
   */
  className?: string
  collapsed?: boolean
}

export const CAccordionButton: FC<CAccordionButtonProps> = ({
  children,
  className,
  collapsed,
  ...rest
}) => {
  const _className = classNames('accordion-button', { ['collapsed']: collapsed }, className)

  return (
    <button className={_className} {...rest}>
      {children}
    </button>
  )
}
